import { useTranslation } from "react-i18next";
import {
  assestsFeedUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../LmsManager.module.css";

const LmsStaffAttendanceListCardPresent = ({
  staff,
  selectedId,
  onPresent,
  staffOtherData,
}) => {
  const { t } = useTranslation();

  const onClick = () => {
    onPresent(staff?._id);
  };

  return (
    <>
      <section
        className={style.lmsstlc_card}
        style={{
          cursor: "pointer",
        }}
        onClick={onClick}
      >
        <img
          src={
            selectedId?.includes(staff?._id)
              ? `${assestsUtilityUrl}/radio-green.svg`
              : `${assestsUtilityUrl}/radio-grey.svg`
          }
          alt="user avatar"
        />
        <p>{staff?.staffROLLNO ?? ""}</p>
        <img
          src={
            staff?.staffProfilePhoto
              ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
              : `${assestsFeedUrl}/feed-user-avatar.svg`
          }
          alt="user avatar"
        />
        <div className={style.lmsstlc_card_inner}>
          <h6>
            {staff?.staffFirstName ?? ""} {staff?.staffMiddleName ?? ""}{" "}
            {staff?.staffLastName ?? ""}
          </h6>
          {/* <p>
            {t("intime")}: {staffOtherData?.inTime ?? ""}, {t("outtime")}:{" "}
            {staffOtherData?.outTime ?? ""}
          </p> */}
        </div>
      </section>
    </>
  );
};

export default LmsStaffAttendanceListCardPresent;
