import { useCallback } from "react";
import LmsStaffAttendanceListCard from "./LmsStaffAttendanceListCard";
import LmsStaffAttendanceListCardAbsent from "./LmsStaffAttendanceListCardAbsent";
import LmsStaffAttendanceListCardPresent from "./LmsStaffAttendanceListCardPresent";

const LmsStaffAttendanceList = ({
  staffOneInstituteList,
  step,
  setState,
  state,
  isUpdate,
  otherData,
}) => {
  const onPresent = useCallback(
    (data) => {
      if (isUpdate) {
        if (state?.present?.includes(data)) {
          setState((prev) => ({
            ...prev,
            present: prev?.present?.filter((dt) => dt !== data),
            absent: [...prev.absent, data],
          }));
        } else if (state?.comingPresent?.includes(data)) {
          setState((prev) => ({
            ...prev,
            comingPresent: prev?.comingPresent?.filter((dt) => dt !== data),
            absent: [...prev.absent, data],
          }));
        } else {
          setState((prev) => ({
            ...prev,
            present: [...prev.present, data],
            comingAbsent: prev?.comingAbsent?.filter((dt) => dt !== data),
            absent: prev?.absent?.filter((dt) => dt !== data),
          }));
        }
      } else {
        if (state?.present?.includes(data)) {
          setState((prev) => ({
            ...prev,
            present: prev?.present?.filter((dt) => dt !== data),
          }));
        } else {
          setState((prev) => ({
            ...prev,
            present: [...prev.present, data],
          }));
        }
      }
    },
    [state?.present, state?.comingPresent, isUpdate]
  );
  const onAbsent = useCallback(
    (data) => {
      if (isUpdate) {
        if (state?.absent?.includes(data)) {
          setState((prev) => ({
            ...prev,
            absent: prev?.absent?.filter((dt) => dt !== data),
            present: [...prev?.present, data],
          }));
        } else if (state?.comingAbsent?.includes(data)) {
          setState((prev) => ({
            ...prev,
            comingAbsent: prev?.comingAbsent?.filter((dt) => dt !== data),
            present: [...prev?.present, data],
          }));
        } else {
          setState((prev) => ({
            ...prev,
            absent: [...prev?.absent, data],
            comingPresent: prev?.comingPresent?.filter((dt) => dt !== data),
            present: prev?.present?.filter((dt) => dt !== data),
          }));
        }
      } else {
        if (state?.absent?.includes(data)) {
          setState((prev) => ({
            ...prev,
            absent: prev?.absent?.filter((dt) => dt !== data),
          }));
        } else {
          setState((prev) => ({
            ...prev,
            absent: [...prev?.absent, data],
          }));
        }
      }
    },
    [state?.absent, state?.comingAbsent, isUpdate]
  );

  return (
    <>
      {step === 1 &&
        staffOneInstituteList?.map((staff) => (
          <LmsStaffAttendanceListCard
            key={staff?._id}
            staff={staff}
            staffOtherData={otherData && otherData[staff?._id]}
            presentId={[...state?.present, ...state?.comingPresent]}
            absentId={[...state?.absent, ...state?.comingAbsent]}
          />
        ))}
      {step === 2 &&
        staffOneInstituteList?.map((staff) => (
          <LmsStaffAttendanceListCardPresent
            key={staff?._id}
            staff={staff}
            selectedId={[...state?.present, ...state?.comingPresent]}
            onPresent={onPresent}
            staffOtherData={otherData && otherData[staff?._id]}
          />
        ))}
      {step === 3 &&
        staffOneInstituteList?.map((staff) => (
          <LmsStaffAttendanceListCardAbsent
            key={staff?._id}
            staff={staff}
            selectedId={[...state?.absent, ...state?.comingAbsent]}
            onAbsent={onAbsent}
          />
        ))}
    </>
  );
};

export default LmsStaffAttendanceList;
