import { useCallback, useMemo, useState, memo, useEffect } from "react";
import style from "../UiTab.module.css";
import dayjs from "dayjs";
import { useCalendar } from "../../Hooks/CustomHooks/use-calendar";
import { assestsUtilityUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
const AttendanceWrapper = ({ onDateSelect, children }) => {
  const [page, setPage] = useState(1);
  const {
    selectedDate,
    getPreviousMonth,
    getNextMonth,
    generateCalendar,
    getToday,
    getSelectedDate,
    getCompareDate,
    monthPage,
  } = useCalendar();

  const withdate = useMemo(() => {
    let arr = [];
    Object.values(generateCalendar)?.map((rw) => {
      rw?.map((dt) => {
        if (dt?.type === "NEXT_MONTH" || dt?.type === "PREVIOUS") {
        } else {
          arr.push(dt);
        }
      });
    });
    return arr;
  }, [generateCalendar]);

  const getTodayDate = useCallback(() => {
    let dt = dayjs(getToday()).format("DD");
    return +dt;
  }, [getToday]);

  useEffect(() => {
    if (getTodayDate() <= 10) {
      setPage((prev) => (prev = 1));
    } else if (getTodayDate() <= 20) {
      setPage((prev) => (prev = 2));
    } else if (getTodayDate() <= 30) {
      setPage((prev) => (prev = 3));
    } else {
      setPage((prev) => (prev = 4));
    }
  }, [getTodayDate]);

  let stating_slice = (page - 1) * 10;

  const plartialDates = useMemo(() => {
    if (withdate?.length > 0) {
      let partialList = withdate?.slice(stating_slice, stating_slice + 10);
      return partialList;
    }
  }, [withdate, stating_slice]);
  const pageCount = useCallback(() => {
    return Math.ceil(withdate?.length / 10);
  }, [withdate]);
  const onDateChoose = (data) => {
    getSelectedDate(data);
    onDateSelect(data);
  };

  const onPrev = () => {
    if (page <= 1) {
      let prev_month = dayjs(selectedDate).get("month") - 1;
      setPage((prev) => (prev = monthPage[prev_month]));
      getPreviousMonth();
    } else {
      setPage((prev) => (prev -= 1));
    }
  };
  const onNext = () => {
    if (pageCount() <= page) {
      setPage((prev) => (prev = 1));
      getNextMonth();
    } else {
      setPage((prev) => (prev += 1));
    }
  };

  // Object.values(generateCalendar)??
  return (
    <div className={style.attendance_card}>
      <div className={style.ac_heading}>
        <h6>
          {dayjs(selectedDate).format("MMMM")}{" "}
          {dayjs(selectedDate).format("YYYY")}
        </h6>
        {children}
      </div>
      <section className={style.attendance_contianer}>
        <div className={style.ac_image} onClick={onPrev}>
          <img
            src={`${assestsUtilityUrl}/next.svg`}
            alt="previous icon"
            style={{
              rotate: "180deg",
            }}
          />
        </div>
        {plartialDates?.map((cl) => (
          <p
            className={
              getCompareDate(dayjs(selectedDate).format("DD/MM/YYYY"), cl?.date)
                ? `${style.ac_date} ${style.ac_date_active}`
                : style.ac_date
            }
            key={cl?.date}
            onClick={() => onDateChoose(cl?.date)}
          >
            <span className={style.ac_date_vale}>{cl?.value}</span>
            <br />
            {cl?.day}
          </p>
        ))}

        <div className={style.ac_image} onClick={onNext}>
          <img src={`${assestsUtilityUrl}/next.svg`} alt="next icon" />
        </div>
      </section>
    </div>
  );
};

export default memo(AttendanceWrapper);
