import style from "../UiTab.module.css";

const AttendanceTabButton = ({
  label,
  onAction,
  isDisabled,
  customStyle,
  children,
}) => {
  const onClick = () => {
    if (isDisabled) {
    } else {
      onAction();
    }
  };
  return (
    <>
      <div onClick={onClick} className={style.atb_btn} style={customStyle}>
        {label}
        {children}
      </div>
    </>
  );
};

export default AttendanceTabButton;
