import { useTranslation } from "react-i18next";
import { useCalendar } from "../../../Hooks/CustomHooks/use-calendar";
import AttendanceWrapper from "../../../Ui/Attendance/AttendanceWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useCallback, useEffect, useState } from "react";
import LmsStaffAttendanceList from "./LmsStaffAttendanceList";
import {
  useStaffGetAttendance,
  useStaffMarkAttendance,
  useStaffOneInstituteList,
  useStaffUpdateAttendance,
} from "../../../Hooks/ApiHooks/StaffManager/staff-manager-api";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import LmsAttendanceTab from "./LmsAttendanceTab";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import dayjs from "dayjs";
const LmsAttendance = ({ instituteId }) => {
  const { t } = useTranslation();
  const { selectedDate, getSelectedDate } = useCalendar();
  const [step, setStep] = useState(1);
  const [state, setState] = useState({
    present: [],
    comingPresent: [],
    absent: [],
    comingAbsent: [],
  });
  const [disabled, setDisabled] = useState(false);

  const [staffMarkAttendance] = useStaffMarkAttendance();
  const [staffUpdateAttendance] = useStaffUpdateAttendance();
  const { staffOneInstituteList, staffOneInstituteListLoading } =
    useStaffOneInstituteList({
      data: {
        id: instituteId,
        page: "",
        limit: "",
        date: "",
      },
      skip: !instituteId,
    });

  const { staffGetAttendance, staffGetAttendanceRefetch } =
    useStaffGetAttendance({
      data: {
        id: instituteId,
        date: `${dayjs(selectedDate).format("DD/MM/YYYY")}`,
      },
      skip: !selectedDate ? true : !instituteId,
    });

  useEffect(() => {
    if (staffGetAttendance?._id) {
      setState((prev) => ({
        ...prev,
        present: [],
        absent: [],
        comingPresent: [...staffGetAttendance?.presentStaff],
        comingAbsent: [...staffGetAttendance?.absentStaff],
      }));
    } else {
      setState((prev) => ({
        ...prev,
        present: [],
        absent: [],
        comingPresent: [],
        comingAbsent: [],
      }));
    }
  }, [staffGetAttendance]);

  const onResetState = () => {
    if (staffGetAttendance?._id) {
      setState(() => ({
        present: [],
        comingPresent: [...staffGetAttendance?.presentStaff],
        absent: [],
        comingAbsent: [...staffGetAttendance?.absentStaff],
      }));
    } else {
      setState((prev) => ({
        present: [],
        comingPresent: prev?.comingPresent,
        absent: [],
        comingAbsent: prev?.comingAbsent,
      }));
    }
  };

  const onRegisterAttendance = useCallback(() => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      let obj = {
        present: [],
        absent: [],
      };
      if (step === 2) {
        obj.present = state.present;
        for (let st of staffOneInstituteList) {
          if (obj.present?.includes(st?._id)) {
          } else {
            obj.absent.push(st?._id);
          }
        }
      } else {
        obj.absent = state.absent;
        for (let st of staffOneInstituteList) {
          if (obj.absent?.includes(st?._id)) {
          } else {
            obj.present.push(st?._id);
          }
        }
      }
      staffMarkAttendance({
        id: instituteId,
        attendance: {
          ...obj,
          date: `${dayjs(selectedDate).format("DD/MM/YYYY")}`,
        },
      }).then(() => {
        staffGetAttendanceRefetch();
        setStep(1);
        setDisabled((pre) => !pre);
      });
    }
  }, [
    step,
    instituteId,
    selectedDate,
    state.present,
    staffOneInstituteList?.length,
    state.absent,
    staffGetAttendanceRefetch,
  ]);
  const onUpdateAttendance = useCallback(() => {
    if (staffGetAttendance?._id) {
      setDisabled((pre) => !pre);
      staffUpdateAttendance({
        aid: staffGetAttendance?._id,
        attendance: {
          present: state.present,
          absent: state.absent,
        },
      }).then(() => {
        staffGetAttendanceRefetch();
        setStep(1);
        setDisabled((pre) => !pre);
      });
    }
  }, [
    staffGetAttendance?._id,
    state.present,
    state.absent,
    staffGetAttendanceRefetch,
  ]);

  const onSave = useCallback(() => {
    if (staffGetAttendance?._id) {
      onUpdateAttendance();
    } else {
      onRegisterAttendance();
    }
  }, [staffGetAttendance?._id, onUpdateAttendance, onRegisterAttendance]);

  const onDateChoose = (data) => {
    getSelectedDate(data);
  };

  let presentCount = 0;
  let absentCount = 0;
  presentCount += state?.present?.length + state?.comingPresent?.length;
  absentCount += state?.absent?.length + state?.comingAbsent?.length;

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <AttendanceWrapper onDateSelect={onDateChoose}>
            {state?.present?.length > 0 || state?.absent?.length > 0 ? (
              <CreateButton
                label={t("save_attendance")}
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 1rem",
                }}
                onAction={onSave}
              />
            ) : null}
          </AttendanceWrapper>
        </UserMemberContentWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBottom: "0",
          }}
        />
        <UserMemberContentWrapper>
          <LmsAttendanceTab
            step={step}
            setStep={setStep}
            onReset={onResetState}
            totalCount={staffOneInstituteList?.length || 0}
            presentCount={presentCount}
            absentCount={absentCount}
          />
          <LmsStaffAttendanceList
            staffOneInstituteList={staffOneInstituteList}
            step={step}
            state={state}
            setState={setState}
            isUpdate={staffGetAttendance?._id ? true : false}
            otherData={staffGetAttendance?.other_data}
          />
          {staffOneInstituteListLoading ? (
            <Loading />
          ) : (
            staffOneInstituteList?.length === 0 && (
              <EmptyMessage title="No staff list found." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default LmsAttendance;
