import { useTranslation } from "react-i18next";
import AttendanceTabButton from "../../../Ui/Attendance/AttendanceTabButton";
import style from "../LmsManager.module.css";

const LmsAttendanceTab = ({
  setStep,
  step,
  onReset,
  totalCount,
  presentCount,
  absentCount,
}) => {
  const { t } = useTranslation();

  const onTotal = () => {
    onReset();
    setStep(1);
  };
  const onPresent = () => {
    onReset();
    setStep(2);
  };
  const onAbsent = () => {
    onReset();
    setStep(3);
  };
  return (
    <div className={style.lmsat_container}>
      <AttendanceTabButton
        label={t("total")}
        customStyle={
          step === 1
            ? {
                "--bg-atb": "#1c023b",
                "--color-atb": "#ffffff",
              }
            : {
                "--bg-atb": "rgba(75, 26, 133, 0.1)",
                "--color-atb": "#121212",
              }
        }
        onAction={onTotal}
      >
        <span
          style={{
            marginLeft: "0.2rem",
          }}
        >
          {totalCount}
        </span>
      </AttendanceTabButton>
      <AttendanceTabButton
        label={t("present")}
        customStyle={
          step === 2
            ? {
                "--bg-atb": "#20b038",
                "--color-atb": "#ffffff",
              }
            : {
                "--bg-atb": "rgba(32, 176, 56, 0.1)",
                "--color-atb": "#121212",
              }
        }
        onAction={onPresent}
      >
        <span
          style={{
            marginLeft: "0.2rem",
          }}
        >
          {presentCount}
        </span>
      </AttendanceTabButton>
      <AttendanceTabButton
        label={t("absent")}
        customStyle={
          step === 3
            ? {
                "--bg-atb": "#ee3833",
                "--color-atb": "#ffffff",
              }
            : {
                "--bg-atb": "rgba(238, 56, 51, 0.1)",
                "--color-atb": "#121212",
              }
        }
        onAction={onAbsent}
      >
        <span
          style={{
            marginLeft: "0.2rem",
          }}
        >
          {absentCount}
        </span>
      </AttendanceTabButton>
    </div>
  );
};

export default LmsAttendanceTab;
